<template>
  <div class="container-tag text-uppercase">
    <span class="tag" :class="classTag">{{ tag(phoneTag) }}</span>
  </div>
</template>

<script lang="ts" setup>
import { computed } from 'vue';

interface Props {
  phoneTag: string;
  classTag?: string;
}

defineProps<Props>();

const tag = computed(() => {
  return (tag: string) => {
    switch (tag.toLowerCase()) {
      case 'home':
        return 'home';
      case 'mobile':
        return 'mobile';
      case 'work':
        return 'work';
      default:
        return 'other';
    }
  };
});
</script>
<style scoped>
.container-tag {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2px 4px;
  background: var(--bg-subtle);
  border-radius: 4px;
}

.tag {
  font-size: 9px;
  font-weight: 600;
  line-height: normal;
  color: var(--text-secondary);
}
</style>
